@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500;600;700&display=swap);
body{margin:0;font-family:'Montserrat', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;background-color:#f0f0f0}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}.App{width:100%;height:100%}

.page-layout{height:100%;max-width:1400px;min-width:320px;margin:0 auto}.page-body{height:100%;width:100%;display:-webkit-flex;display:flex}.user-info{width:100%;height:100%}.island{width:100%;height:auto;margin:30px 0px;box-sizing:border-box;border-radius:6px;background-color:whitesmoke;box-shadow:0px 0px 5px rgba(0,0,0,0.15)}.profile-header{display:-webkit-flex;display:flex;-webkit-flex-direction:row;flex-direction:row;-webkit-align-items:center;align-items:center;-webkit-justify-content:space-around;justify-content:space-around;padding:10px 20px}.island__title{width:100%;font-family:Montserrat;font-weight:500;font-size:1.5rem;padding:10px 20px;box-sizing:border-box;border-bottom:1px solid #e5e5e5;color:rgba(0,0,0,0.7)}@media (max-width: 1920px){.page-layout{max-width:1400px}}@media (max-width: 1600px){.page-layout{max-width:1000px}}@media (max-width: 1024px){.page-layout{max-width:768px}}@media (max-width: 768px){.page-layout{margin:15px}}@media (max-width: 425px){.page-layout{margin:10px}.profile-header{-webkit-flex-direction:column;flex-direction:column}}

.view-metric{position:relative;min-width:120px;min-height:60px;max-height:120px;max-width:60px;background-color:#9c9c9c;border-radius:100px 100px 0px 0px;overflow:hidden}.view-metric__value{position:absolute;width:100px;height:60px;top:10px;left:10px;display:-webkit-inline-flex;display:inline-flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center;z-index:1;border-radius:100px 100px 0px 0px;background-color:#636363}.view-metric__value span{color:whitesmoke;padding-bottom:5px}.view-metric__value img{-webkit-filter:invert(1);filter:invert(1);width:20px}.view-metric__spin{position:absolute;border-radius:100px;border-color:#90e4c6;border-style:solid;border-width:60px;border-right-color:transparent;border-bottom-color:transparent;-webkit-transform:rotate(300deg);transform:rotate(300deg)}

.profile-skill{width:100%;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:flex-start;justify-content:flex-start;box-sizing:border-box;padding:10px 20px;margin-bottom:20px;color:rgba(0,0,0,0.8)}.skill__title{font-weight:500;font-size:1.3rem;margin-bottom:10px;color:rgba(0,0,0,0.7)}.skill__description{font-weight:300;font-size:0.9rem;overflow-wrap:break-word}

.profile-header__identy{width:100%;height:80px;display:-webkit-flex;display:flex;-webkit-flex-direction:row;flex-direction:row;-webkit-justify-content:flex-start;justify-content:flex-start}.profile-icon{height:80px;width:80px;box-sizing:border-box;border-radius:100px;border:2px solid #76e076;background-color:#eaeaea}.profile-title{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:center;justify-content:center;margin-left:20px;color:rgba(0,0,0,0.7)}.profile-title__name{font-size:1.4rem;font-weight:500;margin-bottom:5px}.profile-title__specialization{font-size:0.9rem;font-weight:300}@media (max-width: 425px){.profile-header__identy{margin-bottom:20px}}

